    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Jost', sans-serif;
}
html{
    font-size: 100%;
    height: 100%;
}
html{
    background: #eee;
}
.body{
    background: #eee;
    display: flex !important;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap !important;
    height: 100vh;
    width: 100vw;
}


.box{
    
    top:-60px;
    width: 20vw;
    height:20vh;
    border-radius: 0.5rem;
    
    background: #a5bbdd;
    position: relative;
   /* overflow: hidden;*/
    transform: translate3d(0, 0 ,0 ,0);
    min-width: 25rem;
    min-height: 25rem;
}
