.box::after{
    content: "";
    display: block;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 11;
    transform: translate3d(0,0,0);
}




.info{
    position:absolute;
    bottom: 2;
    width: 100%;
    height:50%;
    z-index: 4;
    font-size: 15px;
    text-align: center;
   

}


.loaction{
    margin-top: 1.5rem;
    text-align: center;
    
}
.temp{
    margin-top: 2.5rem;
    text-align: center;
    font-size: 3rem;
}
.tempmen_max{
    text-align: center;
    margin-top: 0.3rem;
    font-weight: 300;
    font-size: 1.2rem;
    color: #57606f;
}



.wrapper {
    width: 500px;
    height: 500px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-radius: 5px;
    background-image: linear-gradient(to top, rgb(162, 162, 173) 0%, rgb(180, 155, 155) 100%);
    overflow: hidden;
  }
  
  .wave {
    width: 1000px;
    height: 1025px;
    position: absolute;
    top: -25%;
    left: 50%;
    margin-left: -500px;
    margin-top: -500px;
    border-radius: 35%;
    background: #d1e8ed ;
    -webkit-animation: wave 15s infinite linear;
            animation: wave 15s infinite linear;
    
  }
  
  @-webkit-keyframes wave {
    from { transform: rotate(0deg);}
    from { transform: rotate(360deg);}
  }
  
  @keyframes wave {
    from { transform: rotate(0deg);}
    from { transform: rotate(360deg);}
  }
  .inputfield {
    width:50%;

    margin: auto;
    border-radius: 10rem;
    padding: 2px 5px 5px 5xp;
    outline: none;
    font-size: 1.5rem;
    border-color: tomato;

    
    
    
}
.data{
    width:50%;

    margin: auto;
    border-radius: 10rem;
    padding: 2px 5px 5px 5xp;
    outline: none;
    font-size: 1.5rem;
    border-color: tomato;
    margin-top:25px;
}
.fa-street-view{
    color: tomato;
    font-size: 50px;
}
@media only screen and (max-device-width: 960px) {
    .inputfield{
    width:100%;

    margin: auto;


    }
    .temp{
        margin-top: 1.5rem;

    }
    .tempmen_max{
        text-align: center;
        margin-top: 0.3rem;
        font-weight: 200;
        font-size: 1rem;
        color: #57606f;
    
    }
    .fa-street-view{
        color: brown;
        font-size: 25px;
    }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

